.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(18, 20, 23, 0.5);
  z-index: 10;
  opacity: 0;
  cursor: pointer;
  color: #121417;
  transition: opacity, 250ms ease-in-out;

  &.open {
    opacity: 1;
  }
}

.modal {
  position: relative;
  background-color: #fff;
  top: 50%;
  left: 50%;
  //   transform: translate(-50%, -50%);
  width: 541px;
  padding: 40px;
  border-radius: 24px;
  max-height: 95vh;
  overflow: scroll;
  cursor: default;
  transform: translate(-50%, -50%) scale(0.2);
  transition: transform 250ms ease-in-out;

  .backdrop.open & {
    transform: translate(-50%, -50%) scale(1);
  }
}

.closeButton {
  position: absolute;
  top: 16px;
  right: 16px;
  border: transparent;
  background-color: transparent;
}

.closeButtonIcon {
  color: #121417;
  transition: color 250ms ease-in-out;

  &:hover,
  &:focus {
    color: #0b44cd;
  }
}

.carImg {
  height: 248px;
  width: 100%;
  object-fit: cover;
  border-radius: 14px;
}

.carTitle {
  margin-top: 14px;
  margin-bottom: 8px;
  font-size: 18px;
  font-weight: 500;
  line-height: calc(24 / 18);

  & span {
    color: #3470ff;
  }
}

.extraInfoText {
  color: rgba(18, 20, 23, 0.5);
  font-size: 12px;
  font-weight: 400;
  line-height: calc(18 / 12);
}

.carDescription {
  font-size: 14px;
  font-weight: 400;
  line-height: calc(20 / 14);
  margin-top: 14px;
}

.carSybTitle {
  font-size: 14px;
  font-weight: 500;
  line-height: calc(20 / 14);
  margin-top: 24px;
  margin-bottom: 8px;
}

.conditionsList {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}

.conditionsItem {
  padding: 7px 14px;
  border-radius: 35px;
  background-color: #f9f9f9;
  font-size: 12px;
  font-weight: 400;
  line-height: calc(18 / 12);

  & span {
    color: #3470ff;
    font-weight: 600;
  }
}

.rentalButton {
  display: inline-block;
  background: #3470ff;
  color: #fff;
  border-radius: 12px;
  border-color: transparent;
  padding: 12px 50px;
  font-size: 14px;
  font-weight: 600;
  line-height: calc(20 / 14);
  margin-top: 24px;
  transition: background-color 250ms ease-in-out;

  &:hover {
    background-color: #0b44cd;
  }
}
